.homepage {}

.directory-menu {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 20px;
}

.menu-item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1 auto;
  min-width: 30%;
  min-height: 200px;
  background-color: rgb(0, 255, 213);
  border: 1px solid black;
  margin: 5px;

  &:first-child {}

  &:last-child {}

  .content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    padding: 10px;
    border: 1px solid rgb(250, 1, 1);

    .title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a;
    }

    .subtitle {}
  }
}